import navigation from '../../components/navigation/navigation';
import addimg from '../../components/addimg/add-img';
import uniCombox from '../../components/uni-combox/uni-combox';
import pcaPicker from '@/components/pcaPicker/pcaPicker.vue'
export default {
	data() {
		return {
			config: {
				type: 2, //0无title，1有title
				renttype: 1,
				back: true
			},
			type:1,
			userId:'',
			title: '',
			introduce:'',
			require:'',
			inputValue: '',
			inputValue1: '',
			textareaLength: 20,
			textareaLength1: 100,
			textareaLength2: 100,
			images:[],
			prices:[],
			squares:[],
			location:'',
			longitude:0,
			latitude:0,
			hasHouseResource:true,
			unionNum:1,
			unitType:null,
			unitTypes:['一室一厅','两室一厅','三室一厅','三室两厅','其他'],
			unitNum:-1,
			pickershow:false,
			selectedValue:0,
			indicatorStyle: `height: ${uni.upx2px(88)}px;`,
			havecoordinates:0
		}
	},
	components:{
		navigation,
		addimg,
		uniCombox,
		pcaPicker
	},
	onLoad(options) {
		this.config.renttype = options.type;
		this.type = options.type;
		if (uni.getStorageSync('userId')) {
			this.userId = uni.getStorageSync('userId')
			this.getmobile()
		} else{
			setTimeout(() => {
				uni.showToast({
					title: '请先授权登录',
					icon: 'none'
				})
			}, 100);
			uni.switchTab({
			   url: '../my/my'　
			})
		}
	},
	onShow() {
		var pages = getCurrentPages();
		var currPage = pages[pages.length - 1]; //当前页面
		let res = currPage.data.userdata;
		console.log(res)
		if(res){
			this.location = res.address
			this.latitude = res.latitude
			this.longitude = res.longitude
			this.havecoordinates = 1
		}
	},
	watch:{
		title(){
			if(this.title.length>20){
				this.title=this.title.slice(0,20)
				console.log(this.title)
			}
		}
	},
	methods: {
		customConduct() {
			uni.navigateBack({});
		},
		handtextarea1(event) {
			this.introduce = event.target.value;
			console.log(this.introduce);
		},
		handtextarea2(event) {
			this.require = event.target.value;
			console.log(this.require);
		},
		onImgUploaded(e){
			this.images.push(e);
			uni.showToast({
				title: '上传图片成功',
				icon: 'success'
			});
			console.log(this.images);
		},
		onImgDeleted(e) {
			this.images.splice(e,1)
			console.log(this.images);
		},
		showpicker(){
			this.pickershow = true;
		},
		wrapperChange(e){
			let detailValue = e.detail.value[0];
			console.log(e.detail)
			this.selectedValue = detailValue;
		},
		onCancel(){
		    this.pickershow = false;
		},
		onConfirm(){
			this.unitType = this.unitTypes[this.selectedValue]
			this.unitNum = this.selectedValue
			console.log(this.unitNum)
			this.pickershow = false;
		},
		checkresource(e){
			this.hasHouseResource = e.detail.value == 1?true:false;
			console.log(this.hasHouseResource)
		},
		getlocation(){
			uni.navigateTo({
				url:'../add/getmap'
			})
		},
		showPCA(e){
			this.$refs.pcaPicker.show();
		},
		getPCA(e) {
			console.log(e);
			this.location = e;
		},
		handle(){
			if (this.title==''||this.introduce==''||this.prices.length==0||this.squares.length==0||this.unitNum==-1||this.location==''){
				uni.showToast({
					title: '填写信息不完整',
					icon: 'none'
				});
				return;
			}
			let prices = [parseFloat(this.prices[0]),parseFloat(this.prices[1])]
			let squares = [parseFloat(this.squares[0]),parseFloat(this.squares[1])]
			let coordinates = []
			if (this.havecoordinates==1){
				coordinates = [this.latitude.toFixed(6),this.longitude.toFixed(6)]
			}
			let _this = this;
			uni.showModal({
				title:'提示',
				content:'是否确定发布',
				success:function(response){
					if (response.confirm) {
					    console.log('用户点击确定');
						_this.$http.post('/api/publish/',{
							userId:_this.userId,
							type:_this.type,
							title:_this.title,
							introduce:_this.introduce,
							location:_this.location,
							prices:prices,
							squares:squares,
							unitType:_this.unitNum,
							images:_this.images,
							require:_this.require,
							unionNum:_this.unionNum,
							hasHouseResource:_this.hasHouseResource,
							coordinates:coordinates
						}).then(res => {
							console.log(res)
							setTimeout(() => {
								uni.showToast({
									title: '发布成功',
									icon: 'success'
								});
							}, 100);
							uni.switchTab({
								url:'../home/home'
							})
						}).catch((e) => {});
					} else if (response.cancel) {
					    console.log('用户点击取消');
					}
				},
				fail:function(){}
			})
		},
		getmobile(){
			this.$http.get('/api/person/info/', {
				userId: this.userId
			}).then(res => {
				if (!res.mobile) {
					setTimeout(() => {
						uni.showToast({
							title: '请先绑定手机号再发布',
							icon: 'none'
						});
					}, 100);
					uni.navigateTo({
						url: '../my/sms'
					})
				}
			}).catch((e) => {});
		}
	}
}
